<template>
	<div class="CycleCount_Submit">
		<header><i class="iconfont icon-xiangzuo1" @click="sendParams"></i><span>盘点</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div class="Pallet">
				<div>
					<span>库位</span>
					<input readonly v-model="Loc"></input>
				</div>
				<div>
					<span>托盘单号</span>
					<input readonly v-model="CountNo"></input>
				</div>
			</div>
			<div>
				<div>
					<span>商品代码</span>
					<input readonly v-model="Sku"></input>
				</div>
				<div>
					<span>客户代码</span>
					<input readonly v-model="StorerKey"></input>
				</div>
			</div>

			<div class="goodsName">
				<span>商品名称</span>
				<textarea v-model="Descr" readonly></textarea>
			</div>
			<div class="property" v-show="isShowDetail"> 
				<span>批次属性</span>
				<textarea readonly>
货物状态:{{Lottable02}}
生产批号:{{Lottable03}}
生产日期:{{Lottable04}}
到期日期:{{Lottable05}}
收货日期:{{Lottable09}}
收货单号:{{Lottable10}}
系统批次:{{Lot}}
				</textarea>
			</div>
			<div>
				<div>
					<span>系统数量</span>
					<input readonly v-model="Qty"></input>
				</div>
				<div>
					<span>单位</span>
					<input readonly v-model="Susr2"></input>
				</div>
			</div>
			<div>
				<div>
					<span>盘点数量</span>
					<input class="writable" ref="QtyCount"   @change="toUppercase('QtyCount')"  v-model="QtyCount" @keyup.enter="confirmOnclick"></input>
					<i v-if="QtyCount" @click="clearInput('QtyCount')" class="iconfont icon-cuowu"></i>
				</div>
				<div v-show="isShowDetail">
					<span>托盘编号</span>
					<input readonly v-model="PID"></input>
				</div>
			</div>
			<div class="g_switchTab">
				<div @click="previous" class="previous"><i class="iconfont icon-xiangzuo1"></i></div>
				<span>{{CurrIndex}}/{{totalData}}</span>
				<div @click="next" class="next"><i class="iconfont icon-xiangyou1"></i></div>
			</div>
			<span @click='confirmOnclick' class="g_singleBtn">确认</span>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CycleCount_Submit",
		data(){
			return{
				Loc:'',        // 库位
				CountNo:'',    // 盘点单号
				Sku:'',        // 商品代码
				StorerKey:'',  // 客户代码
				Descr:'',      // 商品名称     
				Susr2:'',
				input8:'',
				input9:'',
				input10:'',
				descr:'',
				Lottable02:'', // 货物状态
				Lottable03:'', // 生产批号
				Lottable04:'', // 生产日期
				Lottable05:'', // 到期日期
				Lottable09:'', // 收货日期
				Lottable10:'', // 收货单号
				Lot:'',        // 系统批次
				Qty:'',        // 系统数量
				Susr2:'',      // 单位
				QtyCount:'',   // 盘点数量
				PID:'',        // 托盘编号
				CurrIndex:1,   // 当前页数

				CycleCount_RFCountQry:[],//数据列表
				CycleCount_RFCountQry_HasIndex:[],//数据列表 加了下标
				totalData:0,//一共几条数据
				nowData:1,//当前查看的是第几条数据
				tipsHide:false,//提示信息是否显示
				msg:"",//提示信息
				isShowDetail:true,
				CycleCounted:0,//已盘点商品数量
				inputFocus:"",
			}
		},
		created(){

			var that = this;

			console.log(this.$store.state.CycleCount_RFCountQry)

			if(this.$store.state.CycleCount_RFCountQry.length){
				var CycleCount_RFCountQry = this.$store.state.CycleCount_RFCountQry;
				this.CycleCount_RFCountQry = CycleCount_RFCountQry;

				// 给数组添加下标元素，显示当前页数
				for(let i in this.CycleCount_RFCountQry){
					this.CycleCount_RFCountQry[i]['CurrIndex'] = parseInt(i) + 1;
				}
			}

			//获取从查询页面传来的查询条件
			var CountNo = this.$route.query.CountNo;
			this.CountType = this.$route.query.CountType;
			var Loc = this.$route.query.Loc; 

			if(this.CountType == "PSku"){
				this.isShowDetail = false;
			}

			this.totalData = CycleCount_RFCountQry.length;

			this.Loc = Loc;
			this.CountNo = CountNo;

			if(CycleCount_RFCountQry.length==0){
				this.msg = "没有数据";
				that.tipsShow("Loc");
				return;
			}

			this.nowData = 1;
			this.SwitchingData();
		},
		mounted() {

			this.$refs.QtyCount.focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				this.confirmOnclick();
				
			},
			//切换到上一条数据
			previous:function(){

				if(this.nowData<=1){
					return;
				}
				this.nowData --;

				this.SwitchingData();
			},
			//切换到下一条数据
			next:function(){

				if(this.nowData>=this.CycleCount_RFCountQry.length){
					this.nowData = 0;
				}

				this.nowData ++;

				this.SwitchingData();

				this.$refs.QtyCount.focus();
			},
			SwitchingData:function(){

				var that = this;

				var CycleCount_RFCountQry = this.CycleCount_RFCountQry;
				var data = this.$route.query.CountNo;

				var idx = this.nowData - 1;

				this.Sku = CycleCount_RFCountQry[idx].Sku;
				this.StorerKey = CycleCount_RFCountQry[idx].StorerKey;
				this.Descr = CycleCount_RFCountQry[idx].Descr;

				this.Lottable02 = CycleCount_RFCountQry[idx].Lottable02;
				this.Lottable03 = CycleCount_RFCountQry[idx].Lottable03;
				this.Lottable04 = CycleCount_RFCountQry[idx].Lottable04;
				this.Lottable05 = CycleCount_RFCountQry[idx].Lottable05;
				this.Lottable09 = CycleCount_RFCountQry[idx].Lottable09;
				this.Lottable10 = CycleCount_RFCountQry[idx].Lottable10;
				this.Lot = CycleCount_RFCountQry[idx].Lot;

				this.Qty = CycleCount_RFCountQry[idx].Qty;
				this.Susr2 = CycleCount_RFCountQry[idx].Susr2;
				this.PID = CycleCount_RFCountQry[idx].PID;
				this.CurrIndex = CycleCount_RFCountQry[idx].CurrIndex;


				if(that.$store.state.StateOfGoodsData.length){

					that.StateOfGoodsData = that.$store.state.StateOfGoodsData;

					// 货物状态代码
					for(var j = 0;j < that.StateOfGoodsData.length ; j++){

						if(CycleCount_RFCountQry[idx].Lottable02 == that.StateOfGoodsData[j].Code){

							that.Lottable02 = that.StateOfGoodsData[j].Descr;
						}
					}

				}else{

					var stateUrl = this.HOST + "GoodsStatus.do";	

					that.httpRequest(stateUrl,{Code:"",Descr:""},function(res){

						console.log(res)

						that.StateOfGoodsData = res.data.data;

						that.$store.state.StateOfGoodsData = res.data.data;

						// 货物状态代码
						for(var j = 0;j < that.StateOfGoodsData.length ; j++){

							if(CycleCount_RFCountQry[idx].Lottable02 == that.StateOfGoodsData[j].Code){

								that.Lottable02 = that.StateOfGoodsData[j].Descr;
							}
						}

					})
				}
			},
			
			confirmOnclick(){

				var that = this;
				if(this.QtyCount==''){
					this.msg = "盘点数量不能为空";
					
					that.tipsShow("QtyCount");
					that.inputFocus = "QtyCount";
				}else{
					// console.log("kk" + this.Qty);
					if(!this.CountNo || !this.StorerKey || !this.Sku || !this.Loc){
						this.msg = "盘点单号、客户代码、商品代码、库位。都不能为空。";
						that.tipsShow("CountNo");
						that.inputFocus = "CountNo";
					} else{
						var currentUrl = this.HOST + "RFCountSave.do";

						console.log("接口：" + currentUrl);

						var postData = {
							"CountNo":this.CountNo,
							"CountType":this.CountType,
							"UserID":'admin',
							"StorerKey":this.StorerKey,
							"Sku":this.Sku,
							"Loc":this.Loc,
							"Lottable02":this.Lottable02,
							"Lottable03":this.Lottable03,
							"Lottable04":this.Lottable04,
							"Lottable05":this.Lottable05,
							"Lottable09":this.Lottable09,
							"Lottable10":this.Lottable10,
							"Lot":this.Lot,
							"PID":this.PID,
							"QtyCount":this.QtyCount,
							"Qty":this.Qty,
						}


						this.httpRequest(currentUrl,postData,function(res){

							that.CycleCounted++;
							if(that.CycleCounted == that.totalData){

								that.tipsHide = false;
								that.$router.push({
									name:"CycleCount",
								})
							}else{

								that.QtyCount = "";
								that.CycleCount_RFCountQry.splice(that.nowData - 1,1);
								console.log("删除了第" + (parseInt(that.nowData) - 1) + "元素");
								that.nowData --;
								that.next();
							}
						},"QtyCount")
					}
				}
			},
			sendParams:function(){
				var that = this;
				//返回到上一页（库存查询页面）
				this.$router.push({
					name:"CycleCount",
					query:{
						
					}
				})
			}
		}
	}
</script>
<style scoped lang="less">
.CycleCount_Submit{
	.container{
		font-size: 30/50rem;
		&>div{
			width:100%;
			height: 55/50rem;
			margin-top: 10/50rem;
			display:inline-block;
			position: relative;
			input[readonly]{
				outline: none;
			}
			span{
				width: 140/50rem;
				text-align: right;
			}
			&>div{
				width:400/50rem;
				float: left;
				span{
					display:inline-block;
				}
				input{
					width:220/50rem;	
					height:55/50rem;
					background-color: #f0f0f0;
					padding-left: 3/50rem;
				}
				.writable{
					background-color: #fff;
				}
			}
			&>div:nth-child(2){
				width:350/50rem;
				input{
					width:170/50rem;	
				}
			}
			i.icon-cuowu{
				position: absolute;
				left:320/50rem;
				top: 10/50rem;
				font-size: 38/50rem;
			}
		}
		&>.goodsName{
			height:80/50rem;
			&>span{
				margin-right: 10/50rem;
				float: left;
			}
			&>textarea{
				width:574/50rem;
				height:80/50rem;
				float:left;
				outline:none;
				background-color: #f0f0f0;
				outline: none;
			}
		}
		&>.property{
			&>span{
				float: left;
			}
			height:240/50rem;
			&>textarea{
				width:574/50rem;
				height:240/50rem;
				float: left;
				margin-left: 10/50rem;
				outline: none;background-color: #f0f0f0;
			}
		}
	}
}
// 374像素以下（包含374像素）
@media screen and (max-width: 374px){

	input{
		border:3/50rem solid #bbb;
	}

}
@media screen and (max-width: 320px){

	.CycleCount_Submit{
		.container{
			&>.property{
				height:100/50rem;
				&>textarea{
					height:100/50rem;
				}
			}
			.g_singleBtn{
				margin:0/50rem auto;
			}
		}
	}
}
// 277像素）
@media screen and (max-width: 277px){
	
	.CycleCount_Submit{
		.container{
			&>div{
				span{
					width: 150/50rem;
				}
				&>div{
					width:390/50rem;
				}
				i.icon-cuowu{
					left:340/50rem;
				}
			}
			&>.goodsName{
				height:100/50rem;
				&>textarea{
					height:100/50rem;
				}
			}
			&>.property{
				height:332/50rem;
				&>textarea{
					height:332/50rem;
				}
			}
		}
	}
}
</style>


